.map {
	height: 500px;
	background-color: white;
	padding: 1rem;
	margin-top: 16px;
	margin-bottom: 16px;
	box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}
.map .leaflet-container {
	height: 100%;
}
.info__flag {
	height: 80px;
	width: 100%;
	background-size: cover;
	border-radius: 8px;
}
.info__flag img {
	width: 100px;
	border-radius: 5px;
}
.info__confirmed,
.info__recovered,
.info__deaths {
	font-size: 16px;
	margin-top: 5px;
}
.info__container {
	width: 150px;
}
.info__name {
	font-size: 20px;
	font-weight: bold;
	color: #555;
}
